import {
	detailsArticle,
	getComment,
	comment
} from '../../api/api.js'

export default {
	data() {
		return {
			url: null,
			detailObject: {},
			commentList: [],
			articleId: null,
			commentValue: ''
		}
	},
	computed: {
		user() {
			return this.$store.state.user
		}
	},
	onLoad(options) {
		this.articleId = options.id
		this.detailsArticle()
		this.getComment()
	},
	methods: {
		getComment() {
			getComment({
				articleId: this.articleId 
			}).then(res => {
				if (res.data.status === 'success') {
					this.commentList = res.data.data
				}
			}), err => {
			}
		},
		commitComment(value) {
			let params = {
				articleId: this.articleId,
				auid: this.user.userName
			}
			comment(params).then(res => {
				if (res.data.status === 'success') {
					this.commentValue = ''
					// this.commentList.push(res.data.data.detail.value)
					this.getComment()
					uni.showToast({
						icon: 'none',
						title: '评论成功'
					});
				}
			}), err => {
			}
		},
		detailsArticle() {
			detailsArticle({
				id: this.articleId 
			}).then(res => {
				if (res.data.status === 'success') {
					this.detailObject = res.data.data
					this.url = 'http://' + res.data.data.cover
				}
			}), err => {}
		},
	}
}
